canvas {
  border: 1px solid #ccc;
  box-shadow: 1px 1px 3px rgba(0, 0, 0, 0.3);
  background-color: #f4f4f4;
}

.chart-title {
  text-align: center;
  margin-bottom: 1rem;
}

.chart-legend {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
}

.chart-legend__item {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 1rem;
}

.chart-legend__item__color {
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  margin-right: 0.5rem;
}

.chart-legend__item__label {
  font-size: 0.9rem;
  font-weight: 500;
  text-transform: uppercase;
}
