/* Styles for the Expense Tracker app */

.App {
  font-family: Arial, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
}

.expenses {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90%;
  max-width: 600px;
  margin-top: 30px;
  box-shadow: 0px 2px 6px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  overflow: hidden;
}

.expense-filter {
  margin-bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0px 20px;
}

.expense-filter label {
  font-size: 16px;
  font-weight: 500;
  color: #4a4a4a;
}

.expense-filter select {
  font-size: 16px;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #f3f3f3;
  color: #4a4a4a;
}

.total-expenses {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 500;
  color: #4a4a4a;
}

.expense-list {
  margin-top: 20px;
  width: 100%;
  overflow-y: auto;
}

.expense-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  width: 100%;
  border-bottom: 1px solid #e0e0e0;
}

.expense-item:last-child {
  border-bottom: none;
}

.expense-item .description {
  font-size: 16px;
  font-weight: 500;
  color: #4a4a4a;
}

.expense-item .amount {
  font-size: 16px;
  font-weight: 500;
  color: #4a4a4a;
}

.expense-form {
  width: 90%;
  max-width: 600px;
  margin-top: 20px;
}

.expense-chart {
  width: 100%;
  margin-top: 20px;
}
