.google-login-form {
  max-width: 350px;
  margin: 0 auto;
  padding: 20px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
}

.google-login-form__controls {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.google-login-form__control {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
}

.google-login-form__control label {
  font-weight: 600;
  margin-bottom: 5px;
}

.google-login-form__control input {
  padding: 10px;
  border: none;
  border-radius: 5px;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.1);
}

.google-login-form__control input:focus {
  outline: none;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
}

.google-login-form__actions {
  display: flex;
  justify-content: center;
}

.google-login-form__actions button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #4285f4;
  color: #fff;
}
.google-login-form__actions button:hover {
  cursor: pointer;
  background-color: #3367d6;
  }
  
  .google-login-form__actions button:active {
  box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.3) inset;
  }
  
  .google-login-form__actions button:focus {
  outline: none;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
  }
  
  .google-login-form__actions button:disabled {
  background-color: #ccc;
  color: #888;
  cursor: not-allowed;
  }
  
  .google-login-form__actions .loader {
  display: inline-block;
  width: 15px;
  height: 15px;
  border: 3px solid rgba(0, 0, 0, 0.2);
  border-top-color: #4285f4;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-right: 5px;
  }
  
  @keyframes spin {
  to {
  transform: rotate(360deg);
  }
  }
  
  /* Media Queries */
  @media screen and (max-width: 767px) {
  .google-login-form {
  max-width: 100%;
  }
  }