.expenses-filter {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 1rem;
}

.expenses-filter__control {
  display: flex;
  align-items: center;
  margin-left: auto;
}

.expenses-filter__control label {
  margin-right: 1rem;
  font-weight: bold;
}

.expenses-filter__control select {
  font: inherit;
  padding: 0.5rem 3rem;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.expenses-filter__control select:focus {
  outline: none;
  border-color: #2ecc71;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25), 0 0 0 2px #2ecc71;
}
