.expense-item {
  background-color: #f5f5f5;
  border-radius: 8px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
  display: flex;
  justify-content: space-between;
  margin: 0.5rem 0;
  padding: 0.5rem;
  width: 100%;
}

.expense-item__date {
  color: #8f8f8f;
  font-size: 0.8rem;
  font-weight: bold;
  margin-right: 1rem;
}

.expense-item__title {
  font-size: 1.2rem;
  font-weight: bold;
  flex: 1;
}

.expense-item__price {
  font-size: 1.2rem;
  font-weight: bold;
  margin-left: 1rem;
}
